@import "../../styles/global.scss";

.placard {
  width: 924px;
  height: 354px;
  padding: 100px 0 100px 0;
  position: relative;

  &-board {
    width: 100%;
    height: 100%;
    margin: 0 auto 0 auto;
    background-image: url("../../assets/Placard.png");
    background-repeat: no-repeat;
    background-size: cover;
  }

  @media screen and (max-width: $desktopWidth), screen and (max-height: $desktopHeight) {
    width: 700px;
    height: 268px;
  }

  @media screen and (max-width: $tabletLandscapeWidth), screen and (max-height: $tabletLandscapeHeight) {
    width: 550px;
    height: 210px;
  }

  @media screen and (max-width: $tabletPortraitWidth), screen and (max-height: $tabletPortraitHeight) {
    width: 406px;
    height: 154px;
  }

  @media screen and (max-width: $mobileWidth), screen and (max-height: $mobileHeight) {
    width: 242px;
    height: 92px;
  }

  @keyframes dogRunningDesktop {
    0% {
      top: 0;
      left: 0;
      transform: rotate(0);
    }

    30% {
      top: 0;
      left: 820px;
      transform: rotate(0);
    }
    33% {
      top: 50px;
      left: 870px;
      transform: rotate(90deg);
    }

    47% {
      top: 390px;
      left: 870px;
      transform: rotate(90deg);
    }
    50% {
      top: 450px;
      left: 820px;
      transform: rotate(180deg);
    }

    77% {
      top: 450px;
      left: -90px;
      transform: rotate(180deg);
    }
    80% {
      top: 390px;
      left: -140px;
      transform: rotate(270deg);
    }

    97% {
      top: 50px;
      left: -140px;
      transform: rotate(270deg);
    }
    100% {
      top: 0;
      left: -70px;
      transform: rotate(360deg);
    }
  }

  @keyframes dogRunningTL {
    0% {
      top: 20px;
      left: 0;
      transform: rotate(0);
    }

    30% {
      top: 20px;
      left: 610px;
      transform: rotate(0);
    }
    33% {
      top: 70px;
      left: 660px;
      transform: rotate(90deg);
    }

    47% {
      top: 320px;
      left: 660px;
      transform: rotate(90deg);
    }
    50% {
      top: 370px;
      left: 610px;
      transform: rotate(180deg);
    }

    77% {
      top: 370px;
      left: -70px;
      transform: rotate(180deg);
    }
    80% {
      top: 320px;
      left: -120px;
      transform: rotate(270deg);
    }

    97% {
      top: 70px;
      left: -120px;
      transform: rotate(270deg);
    }
    100% {
      top: 20px;
      left: -40px;
      transform: rotate(360deg);
    }
  }

  @keyframes dogRunningTP {
    0% {
      top: 20px;
      left: 0;
      transform: rotate(0);
    }

    30% {
      top: 20px;
      left: 470px;
      transform: rotate(0);
    }
    33% {
      top: 70px;
      left: 520px;
      transform: rotate(90deg);
    }

    47% {
      top: 270px;
      left: 520px;
      transform: rotate(90deg);
    }
    50% {
      top: 320px;
      left: 470px;
      transform: rotate(180deg);
    }

    77% {
      top: 320px;
      left: -70px;
      transform: rotate(180deg);
    }
    80% {
      top: 270px;
      left: -120px;
      transform: rotate(270deg);
    }

    97% {
      top: 70px;
      left: -120px;
      transform: rotate(270deg);
    }
    100% {
      top: 20px;
      left: -40px;
      transform: rotate(360deg);
    }
  }

  @keyframes dogRunningTM {
    0% {
      top: 35px;
      left: 0;
      transform: rotate(0);
    }

    30% {
      top: 35px;
      left: 330px;
      transform: rotate(0);
    }
    33% {
      top: 85px;
      left: 380px;
      transform: rotate(90deg);
    }

    47% {
      top: 205px;
      left: 380px;
      transform: rotate(90deg);
    }
    50% {
      top: 255px;
      left: 330px;
      transform: rotate(180deg);
    }

    77% {
      top: 255px;
      left: -70px;
      transform: rotate(180deg);
    }
    80% {
      top: 205px;
      left: -90px;
      transform: rotate(270deg);
    }

    97% {
      top: 70px;
      left: -90px;
      transform: rotate(270deg);
    }
    100% {
      top: 35px;
      left: -40px;
      transform: rotate(360deg);
    }
  }

  @keyframes dogRunningMobile {
    0% {
      top: 60px;
      left: 0;
      transform: rotate(0);
    }

    30% {
      top: 60px;
      left: 200px;
      transform: rotate(0);
    }
    33% {
      top: 80px;
      left: 220px;
      transform: rotate(90deg);
    }

    47% {
      top: 170px;
      left: 220px;
      transform: rotate(90deg);
    }
    50% {
      top: 190px;
      left: 200px;
      transform: rotate(180deg);
    }

    77% {
      top: 190px;
      left: -40px;
      transform: rotate(180deg);
    }
    80% {
      top: 170px;
      left: -60px;
      transform: rotate(270deg);
    }

    97% {
      top: 70px;
      left: -60px;
      transform: rotate(270deg);
    }
    100% {
      top: 60px;
      left: -20px;
      transform: rotate(360deg);
    }
  }

  &-animation {
    position: absolute;
    background-image: url("../../assets/dogRunningAnimation.gif");
    background-repeat: no-repeat;
    background-size: contain;
    width: 200px;
    height: 100px;
    animation: dogRunningDesktop 6s linear infinite;

    @media screen and (max-width: $desktopWidth), screen and (max-height: $desktopHeight) {
      width: 160px;
      height: 80px;
      animation: dogRunningTL 6s linear infinite;
    }

    @media screen and (max-width: $tabletLandscapeWidth), screen and (max-height: $tabletLandscapeHeight) {
      width: 140px;
      height: 70px;
      animation: dogRunningTP 6s linear infinite;
    }

    @media screen and (max-width: $tabletPortraitWidth), screen and (max-height: $tabletPortraitHeight) {
      width: 120px;
      height: 60px;
      animation: dogRunningTM 6s linear infinite;
    }

    @media screen and (max-width: $mobileWidth), screen and (max-height: $mobileHeight) {
      width: 80px;
      height: 40px;
      animation: dogRunningMobile 6s linear infinite;
    }
  }
}
